import React from 'react';
import styled from 'styled-components';

const InfoDescriptions = styled.div`
  text-align: center;
`;
const Description1 = styled.div``;
const Description2 = styled.div``;
const H = styled.h1`
  padding: 40px;
  font-size: 34px;
`;
const P = styled.p`
  line-height: 35px;
  color: #653c2a;
  font-weight: bold;
`;

const Img = styled.img`
  width: 50%;
  height: 500px;
  padding-top: 60px;
  margin: auto;
`;

const OurInfo = () => {
  return (
    <>
      <InfoDescriptions>
        <Description1>
          <H>글램핑이란?</H>
          <P>
            글램핑이라는 단어는 2005년 영국에서 처음 등장했고 2016년 옥스퍼드
            영어사전에 추가되었다. <br />
            단어 자체로는 새롭게 만들어진 것이지만, 호화로운 텐트 생활
            <br />
            또는 다른 캠핑 시설에서 지내는 것이라는 글램핑의 개념은 전혀 새로운
            것이 아니다.
            <br /> 16세기에 스코틀랜드의 아돌 백작은 제임스 5세와 그의 어머니를
            위해 하이랜드에서 호화로운 캠핑을 준비했다.
            <br /> 아돌은 호화로운 텐트를 치고 그 안에 궁전의 모든 양식을
            채웠다.
          </P>
          <Img src={require('../Image/temp3.jpg')} alt="temp" />
        </Description1>
        <Description2>
          <H>글램핑의 술탄 연회</H>
          <P>
            오스만 술탄들이 사용하는 텐트 내 외부의 절묘한 장식은 텐트를
            지배자에 걸맞는 위엄있는 주거지로 만들었다. <br />
            의례적인 행사에서 텐트는 화려한 연극을 연출하는데 사용되었다. <br />{' '}
            우리는 그것을 황실 텐트장에서 있었던 연회를 묘사한 미니어처
            그림들에서 생생하게 볼 수 있다. <br />
            황실의 텐트들은 마치 경기장처럼 화려하게 장식되어 있었고,
            <br /> 그 장식들은 대개 다른 색깔의 천을 사용한 작품
          </P>
          <Img src={require('../Image/temp4.jpg')} alt="temp" />
        </Description2>
      </InfoDescriptions>
    </>
  );
};

export default OurInfo;
