import React from 'react';
import styled from 'styled-components';
import Section1 from './Section1';

const HContainer = styled.div`
  height: 500px;
  display: flex;
  position: relative;
`;
const RImage = styled.img`
  width: 100%;
  height: 100%;
  float: right;
  filter: blur(3px);
`;
const HomeLeft = styled.div`
  flex-basis: 80%;
  background: linear-gradient(150deg, #12121a, 80.56%, #111119);
`;
const HomeRight = styled.div`
  flex-basis: 30%;
  left: -4px;
  position: relative;
`;

const HomeCenter = styled.div`
  position: absolute;
  top: 75px;
  right: 250px;
  box-shadow: 7px 2px 35px 1px rgba(70, 70, 70, 0.9);
`;

const CImage = styled.img`
  width: 350px;
  height: 350px;
`;

const HCarousel = styled.div`
  position: absolute;
  top: 25%;
  left: 23%;
`;

const H = styled.h1`
  color: white;
  font-size: 34px;
  line-height: 48px;
`;
const P = styled.p`
  color: #999799;
  font-size: 18px;
  line-height: 24px;
`;
const Bar = styled.div`
  padding-top: 40px;
`;

const Content = styled.div`
  margin: auto;
  width: 1px;
  height: 23px;
  opacity: 0.6;
  background-color: black;
`;

const Home = () => {
  return (
    <>
      <HContainer>
        <HomeLeft></HomeLeft>
        <HomeRight>
          <RImage src={require('../Image/tamta.jpg')} alt="HomeImage" />
        </HomeRight>
        <HomeCenter>
          <CImage src={require('../Image/model.jpg')} alt="HomeImage2" />
        </HomeCenter>

        <HCarousel>
          <H>
            감성적이면서 <br />
            매혹적인 글램핑 여행!
          </H>
          <P>
            Veniam Lorem elit id sint qui qui id id eu
            <br /> reprehenderit dolor ullamco dolore.
          </P>
        </HCarousel>
      </HContainer>
      <Bar>
        <Content></Content>
      </Bar>
      <Section1 />
    </>
  );
};

export default Home;
