import React from 'react';
import styled from 'styled-components';
import Footer from './FD';

const Title = styled.div`
  text-align: center;
  margin-top: 30px;
  font-size: 34px;
  font-weight: bold;
`;
const CarouselBox = styled.div`
  display: flex;
  justify-content: center;
  padding-top: 34px;
  flex-wrap: wrap;
`;
const FirstImg = styled.div`
  position: relative;
  width: 300px;
  height: 400px;
  margin: 50px 40px;
`;
const SecondImg = styled.div`
  position: relative;
  width: 400px;
  height: 530px;
  margin: 0px 20px;
`;
const ThirdImg = styled.div`
  position: relative;
  width: 350px;
  height: 450px;
  margin: 30px 10px;
`;

const FImg = styled.img`
  width: 100%;
  height: 100%;
`;
const SImg = styled.img`
  width: 100%;
  height: 100%;
`;
const TImg = styled.img`
  width: 100%;
  height: 100%;
`;

const H = styled.h1`
  position: absolute;
  bottom: 29px;
  left: 29px;
  color: white;
`;
const P = styled.p`
  position: absolute;
  bottom: 29px;
  left: 29px;
  color: white;
`;

const Content = styled.div`
  position: absolute;
  margin: 0px -110px;
  width: 100%;
  background-color: grey;
  height: 2px;
  margin-top: 20px;
`;
const Section2 = () => {
  return (
    <>
      <Title>새로운 아이템</Title>
      <CarouselBox>
        <FirstImg>
          <FImg src={require('../Image/new6.jpg')} alt="tempImg" />
          <H>콜핑 카라코람</H>
          <P>Byee!#21321</P>
        </FirstImg>
        <SecondImg>
          <SImg src={require('../Image/new5.jpg')} alt="tempImg" />
          <H>이지캠프 터티텐트</H>
          <P>Byee!#12551</P>
        </SecondImg>

        <ThirdImg>
          <TImg src={require('../Image/new4.jpg')} alt="tempImg" />
          <H>포레스트룸</H>
          <P>Byee!#65675</P>
          <Content></Content>
        </ThirdImg>
      </CarouselBox>
      <Footer />
    </>
  );
};

export default Section2;
