import React from 'react';
import styled from 'styled-components';
import Section2 from './Section2';

const Title = styled.div`
  text-align: center;
  margin-top: 30px;
  font-size: 34px;
  font-weight: bold;
`;

const ItemList = styled.div`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
`;

const SecImage = styled.img`
  width: 370px;
  height: 250px;
  padding-top: 35px;
  margin: 0px 20px;
`;

const ItemBox = styled.div`
  position: relative;
`;

const H = styled.h1`
  font-size: 16px;
  padding: 0px 20px;
`;

const P = styled.p`
  color: #999799;
  font-size: 15px;
  padding: 0px 20px 60px 20px;
`;
const Bar = styled.div`
  padding-top: 25px;
`;

const Content = styled.div`
  margin: auto;
  width: 1px;
  height: 23px;
  opacity: 0.6;
  background-color: black;
`;
const EventTag = styled.div`
  position: absolute;
  top: 22px;
  left: 20px;
  background: #191623;
  color: white;
  padding: 12px;
  font-size: 12px;
`;

const Section1 = () => {
  return (
    <>
      <Title>이벤트 아이템</Title>

      <ItemList>
        <ItemBox>
          <SecImage src={require('../Image/new2.jpg')} alt="secImage2" />
          <H>콜핑 카라코팜</H>
          <P>대여금액 : 35,000원 </P>
          <EventTag>Event Item</EventTag>
        </ItemBox>

        <ItemBox>
          <SecImage src={require('../Image/new1.jpg')} alt="secImage3" />
          <H>콜맨 아테나 SCR05 2룸텐트</H>
          <P>대여금액 : 57,000원</P>
          <EventTag>Event Item</EventTag>
        </ItemBox>

        <ItemBox>
          <SecImage src={require('../Image/new3.jpg')} alt="secImage1" />
          <H>캠프타운 터널타프</H>
          <P>대여금액 :30,000원</P>
          <EventTag>Event Item</EventTag>
        </ItemBox>
      </ItemList>

      <Bar>
        <Content></Content>
      </Bar>

      <Section2 />
    </>
  );
};

export default Section1;
