import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const Nav = styled.div`
  height: 150px;
  text-align: center;
`;
const NavLogo = styled.div`
  padding: 30px 0px;
`;
const H1 = styled(Link)`
  font-size: 34px;
  font-weight: 900;
  cursor: pointer;
  color: black;
`;

const NavMenu = styled.ul`
  display: flex;
  font-weight: 900;
`;
const NavItems = styled(Link)`
  margin: 0px 22px;
  cursor: pointer;
`;

const Container = styled.div`
  align-items: center;
  display: flex;
  justify-content: center;
`;

const Navbar = () => {
  return (
    <>
      <Nav>
        <NavLogo>
          <H1 to="/">GP</H1>
        </NavLogo>
        <Container>
          <NavMenu>
            <NavItems to="/OurSite">About Glam</NavItems>
            <NavItems to="/OurItems">Items</NavItems>
            <NavItems to="/Service">Service</NavItems>
          </NavMenu>
        </Container>
      </Nav>
    </>
  );
};

export default Navbar;
