import React, { useState } from 'react';
import styled from 'styled-components';
import OurInfo from './OurInfo';

const About = styled.div``;
const Banner = styled.div`
  position: relative;

  height: 500px;
  background: linear-gradient(130deg, #d8d4d8, 30.56%, #fbf7fb, #e0dde0);
  text-align: center;
`;

const Image = styled.img`
  width: 170px;
  height: 170px;
  border-radius: 50%;
  margin: 0px 40px;

  @media screen and (max-width: 768px) {
    width: 120px;
    height: 120px;
  }
`;

const ChoiceImg = styled.img`
  width: 260px;
  height: 260px;
  border-radius: 50%;
  @media screen and (max-width: 768px) {
    width: 120px;
    height: 120px;
  }
`;
const ImageBox = styled.div`
  padding-top: 100px;
`;

const Deco = styled.div`
  font-size: 40px;
  opacity: 0.1;
  font-style: italic;
`;

const H = styled.h1`
  position: absolute;
  color: #a69a99;
  bottom: 30px;
`;

const Page = styled.div`
  left: 75px;
  color: #a69a99;
  position: absolute;
  bottom: 53px;
`;

const OurSite = () => {
  const [carousel, setCarousel] = useState(0);
  let allSlide = 4;

  const nextSlide = () => {
    if (carousel >= allSlide) {
      setCarousel(0);
    } else {
      setCarousel(carousel + 1);
    }
  };

  const prevSlide = () => {
    if (carousel <= 0) {
      setCarousel(4);
    } else {
      setCarousel(carousel - 1);
    }
  };

  return (
    <>
      <About>
        <Banner>
          <ImageBox>
            <Image src={require(`../Image/${carousel}.jpg`)} alt="Site" />
            <Image src={require(`../Image/${carousel + 1}.jpg`)} alt="Site" />
            <ChoiceImg
              src={require(`../Image/${carousel + 2}.jpg`)}
              alt="Site"
            />
            <Image src={require(`../Image/${carousel + 3}.jpg`)} alt="Site" />
            <Image src={require(`../Image/${carousel + 4}.jpg`)} alt="Site" />
          </ImageBox>
          <Deco>Variante de glamour</Deco>
          <H style={{ left: '120px' }} onClick={nextSlide}>
            <i class="fas fa-angle-right"></i>
          </H>
          <Page>{carousel + '/' + `${allSlide}`}</Page>
          <H style={{ left: '40px' }} onClick={prevSlide}>
            <i class="fas fa-angle-left"></i>
          </H>
        </Banner>
      </About>
      <OurInfo />
      {/* 0 8 5 3*/}
    </>
  );
};

export default OurSite;
