import { createGlobalStyle } from 'styled-components';
import reset from 'styled-reset';

const GlobalStyle = createGlobalStyle`

${reset};
    *{
        padding:0px;
        margin:0px;
    }
    body{
        font-family: 'Roboto', sans-serif;
    }

    li{
        list-style:none;
        
    }

    h1 {
        font-size: 28px;
        font-weight: bold;
        color: black;
        margin: 16px 0px;
    }

    h2 {
        font-size: 24px;
        font-weight:400;
        color:black;
        margin: 16px 0px;
    }

    a{
        text-decoration:none;
        color: #999799;
    }
    
    


`;

export default GlobalStyle;
